










.dpage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}
.dpage .photo-container{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    top: 0;
    left: 0;
    z-index: 999999;
}
.dpage .landing{
    height: 60vh;
    background: linear-gradient( to bottom, rgba(0, 0, 0, 0.5),rgba(0, 0, 0, 0.5)), url('../../../img/tm-img-03.jpg');
    background-size: cover;
    background-position: bottom;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.dpage .landing h2{
    color: #fff;
    font-size: 5rem;
    line-height: 1.2;
}
.dpage .landing p{
    font-size: 1.3rem;
    color: #bbb;
    line-height: 1.2;
}
.dpage .landing .scrollBtn{
    position: relative;
    margin-top: 1.2em;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: var(--themeColor);
}
.dpage .landing .scrollBtn::before{
    position: absolute;
    content: '>';
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 2rem;
    transform: rotate(90deg);
}
/* Gallery */
.gallery{
    text-align: center;
    font-size: 1.3rem;
    line-height: 1.2;
    padding: 1.2em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 1rem;
    text-align: center;
    line-height: 1.2;
}
.gallery h2{
    margin-top: 2rem;
    text-transform: uppercase;
    font-size: 2rem;
    text-align: center;
    line-height: 1.2;
    margin-bottom: .5rem;
}
.gallery p{
    margin-bottom: 2rem;
}
.photos{
    width: 100%;
    margin: 2rem 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(16em, 1fr));
}
.photos img{
    width: 100%;
    vertical-align: top;
}
.photos img:hover{
    filter: brightness(.5);
    transition: filter 0.3s ease-in;
}








@media screen and (min-width: 48em){
    .photos{
        width: 80%;
    }
    .photos img{
        height: 200px;
    }
    
    .gallery p{
        width: 60%;
    }
}










/* ImageViewerPage */
.imageViewer{
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    line-height: normal;
    font-size: 1.3rem;
}
.imageViewer img{
    width: 65%;
    height: 90%;
}
.imageViewer .ivBtn{
    z-index: 100;
    position: absolute;
    display: flex;
    flex-direction: row;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
    width: 100%;
    justify-content: space-between;
    padding: 0 1.2em;
}
.imageViewer .ivBtn img{
    cursor: pointer;
    width: 30px;
}
.imageViewer .ivBtn img:hover{
    transform: scale(1.2);
}
.imageViewer .ivBtn .hide{
    display: none;
}

.imageViewer-dashboard{
    height: 50px;
    width: 100%;
    position: fixed;
    background-color: #fff;
    top: 0;
    left: 0;
    box-shadow: 1px 1px 25px -7px #ddd;
    font-size: .8rem;
    display: flex;
    align-items: center;
    padding: 0 1.2rem;
    z-index: 100;
}
.imageViewer-dashboard span{
    background-color: rgb(248, 247, 247);
    padding: .5rem .8rem;
    border-radius: 3rem;
    color: var(--themeColor);
}
.imageViewer-content{
    height: calc(100% - 50px);
    margin-top: 50px;
    display: flex;
    flex-direction: column;
}
.imageViewer-image-box{
    flex: 1;
    min-height: 50%;
    position: relative;
}
.imageViewer-image-box img{
    width: 100%;
    height: 100%;
}
.imageViewer-image-box .imageViewer-image-spices{
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2rem;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: .8rem;
}
.imageViewer-image-box .imageViewer-image-spices p{
    margin: 0 1rem;
}
.imageViewer-image-details{
    min-height: 50%;
    padding: 1.2rem;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #555;
}


@media screen and (min-width: 50em){
    .imageViewer-content{
        flex-direction: row;
    }
}