/* Landing */
.tourslanding{
    height: calc(100vh - 70px);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.68), rgba(0, 0, 0, 0.68)), url('../../../img/banner.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
    display: flex;
    justify-content: center;
    align-items: center;
}
.tourslanding h2{
    font-size: 4rem;
    text-transform: uppercase;
    color: #fff;
}
.tourslanding div{
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
}
.tourslanding div span{
    background-color: #fff;
    margin: 5px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
}
.tourslanding div .big{
    width: 30px;
    height: 30px;
}
/* Intro */
.toursIntro{
    padding: 5rem;
    background-color: rgb(248, 247, 247);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.toursIntro h2{
    text-transform: uppercase;
    margin-bottom: 1rem;
    font-size: 2rem;
    text-align: center;
    line-height: 1.2;
}
.toursIntro p{
    
    font-size: 1rem;
    text-align: center;
    line-height: 1.2;
}
.toursIntro strong{
    color: black;
    font-weight: 600;
}
.toursIntro button{
    width: 200px;
    padding: 10px;
    margin-top: 1rem;
    background-color: #333;
    color: var(--themeColor);
    border: transparent;
    font-size: 1.2rem;
}

/* Destinations */

.destiny .imageholder{
    position: relative;
    height: 400px;
}
.destiny .imageholder .dbtns{
    width: 89px;
    bottom: 2rem;
    right: 1rem;
    position: absolute;
    display: flex;
    justify-content: space-between;
}
.destiny .imageholder .dbtns button{
    z-index: 100;
    outline: none;
    padding: 15px;
    background-color: rgba(0, 0, 0, 0.363);
    border: transparent;
}
.destiny img{
    vertical-align: middle;
    width: 100%;
    height: 100%;
}

.description h2{
    font-size: 2rem;
    text-align: center;
    line-height: 1.2;
    margin-bottom: .5rem;
}
.description{
    flex: 1;
    display: flex;
    align-items: center;
    position: relative;
    font-size: 1rem;
    text-align: center;
    line-height: 1.2;
}
.description div{
    padding: 15% 50px;
    background: var(--themeColor);
}
.altdescr div{
    background: #333;
    color: var(--themeColor);
}
.description div p, .description div h2{
    color: #fff;
    text-align: left;
}
.altdescr div h2{
    color: #555;
    text-align: left;
}
.altdescr div p{
    color: var(--themeColor);
    text-align: left;
}

@media screen and (min-width: 48em){
    .destiantion-container{
        width: 100%;
        display: flex;
        flex-direction: row;
    }
    .alt{
        flex-direction: row-reverse;
    }
    .imageholder{
        flex: 1;
    }
    .destiny .imageholder .dbtns{
        bottom: 50%;
        right: 5%;
    }
    .destiny .imageholder .altbtn{
        left: 5%;
    }
    .description div{
        height: 80%;
        position: absolute;
        left: -5%;
    }
    .altdescr div{
        right: -5%;
    }
}

@media screen and (min-width: 1125px){
    /* Intro */
    .toursIntro p{
        width: 80%;
    }
    .description div{
        height: 64%;
        margin-right: 100px;
        padding: 50px;
    }
    .altdescr{
        margin: 0 -100px 0 100px;
    }
}