hr{
    width: 70%;
    margin: 1.2em auto;
}
/* Intro section */
.aboutInro{
    padding: 10em 1.2em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url('../../../img/bg-header-mobile.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: right;
}
.aboutInro h1{
    font-size: 1.6em;
    line-height: 1;
}
.aboutInro p{
    font-size: 22px;
    line-height: 2.5;
}
.aboutInro button{
    background: linear-gradient(to right, var(--themeColor), lightskyblue);
    font-size: 18px;
    border: transparent;
    outline: none;
    color: white;
    padding: 8px 25px;
    border-radius: 25px;
}
/* About section */
.aboutsection{
    line-height: 1.5;
    padding: 1.2em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 18px;
}
.aboutsection h1{
    color: #333;
    font-weight: 700;
    font-size: 2em;
    text-align: center;
    line-height: 1;
    margin-bottom: 1em;
}
.aboutsection li{
    margin: 1.4em 0;
}
.aboutsection dt{
    border-radius: 18px 0 0 18px;
    height: 34px;
    display: flex;
    align-items: center;
    font-size: 18px;
    color: #333;
    font-weight: 600;
    background-color: rgba(13, 185, 197, 0.089);
}
.aboutsection dt span{
    font-size: 16px;
    height: 100%;
    background-color: var(--themeColor);
    color: #fff;
    padding: 0 18px;
    border-radius: 18px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
/* Team section */
.teamSection{
    padding: 1.2em;
    font-size: 22px;
    line-height: normal;
    background-color: #333;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.teamSection .team-container{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(6em, 1fr));
    grid-row-gap: 1.2em;
    margin: 1.2em 0;
}
.teamSection  .member{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.teamSection  .member img{
    width: 80px;
    height: 80px;
    margin-bottom: 10px;
    border-radius: 50%;
    padding: 3px;
    border: 3px solid var(--themeColor);
}
.teamSection  .member p{
    font-size: 16px;
    margin: 0 1.2em;
    font-weight: 700;
}
.teamSection  .member p span{
    font-weight: 500;
    color: var(--themeColor);
}

.pledge h3{
    color: var(--themeColor);
    font-size: 1.2em;
}
.pledge p{
    margin: 0.5em 0;
    font-size: 18px;
}

/* Testimonial section */
.testimonialSection{
    padding: 1.2em;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.testimonies{
    width: 100%;
    font-size: 18px;
    line-height: 1.5;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(17em, 1fr));
    grid-gap: 1.2em;
}
.testimonies .user{
    margin-top: 2em;
    background-color: #333;
    border-radius: 8px;
    padding: 1.2em;
    position: relative;
    height: 150px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}
.testimonies img{ 
    width: 70px;
    height: 70px;
    border-radius: 50%;
    padding: 3px;
    border: 3px solid var(--themeColor);
}
.testimonies .user .profile{
    position: absolute;
    margin-top: -3.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: var(--themeColor);
}
.testimonies .user{
    position: relative;
}
.testimonies .user span{
    padding: 1.2em;
    position: absolute;
    bottom: 0;
}
/* Offices map section */
.offices{
    padding: 1.2em;
}
#googleMap{
    background-color: #333;
    height: 400px;
}

@media screen and (min-width: 48em){
/* About section */
.aboutsection{
    flex-direction: row;
    align-items: flex-start;
}
.aboutsection h1{
    text-align: left;
    margin-bottom: 14px;
}
.aboutsection li{
    margin: 0;
    margin-bottom: 1.2em;
}
.aboutsection dt{
    margin-bottom: 0.8em;
}
.aboutsection dt{
    background: transparent
}
.divA{
    width: 50%;
    margin-right: 2em;
}
.divB{
    width: 50%;
    margin-left: 1.6em;
}

/* Team section */
.teamSection .team-container{
    width: 50%;
}
.pledge{
    width: 50%;
    text-align: center;
}  


/* Testimonial section */
.testimonies{
    width: 80%;
    line-height: 1;
}
.testimonies .user .profile{
    margin-top: -4.2em;
}

}

@media screen and (min-width: 1125px){
/* Intro section */
    .aboutInro h1{
        font-size: 2.2em;
    }
/* About section */ 
    .aboutsection{
        padding: 1.6em 12em;
    }
    .aboutsection h1{
        width: 70%;
    }
    .aboutsection li{
        margin-bottom: 2em;
    }
    .aboutsection dt{
        margin: 0;
        margin-bottom: 5px;
    }
    .divA{
        width: 60%;
        margin-right: 1.3em;
    }
    .divA p{
        width: 80%;
    }

    /* Team section */
    .teamSection .team-container{
        width: 60%;
    }
}