.about{
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    line-height: normal;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1em;
}
.about-container{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.about-container div{
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin: 14px 0;
}
.about-container div img{
    height: 200px;
}
.about-container div p{
    font-size: 1em;
    margin: 0 1.2em;
    width: 40%;
}

@media screen and (max-width: 48em){
    .about-container div{
        flex-direction: column;
    }
    .about-container div img{
        margin: 0 auto;
        width: auto;
        height: 100px;
    }
    .about-container .b{
        flex-direction: column-reverse;
    }
    .about-container div p{
        width: 90%;
        margin: 0 auto;
    }
}

@media screen and (min-width: 1125px){
    .about-container{
        width: 80%;
    }
    .about-container div img{
        height: 250px;
    }
    .about-container div p{
        font-size: 1em;
        margin: 0 1.2em;
        width: 40%;
    }    
}
