.footer{
    padding: 1.2em;
    font-size: 1rem;
    background-color: #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.footer h2{
    font-size: 1.3rem;
    text-transform: uppercase;
}
.footer p{
    line-height: 1.2rem;
}
.footer li{
    line-height: 1.5;
}
.footer .fcontainer{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(12em, 1fr));
    grid-gap: 14px;
}
.designers{
    margin-top: 3rem;
}
.designers span{
    color: rgb(218, 0, 0);
}


@media screen and (min-width: 48em){
    .footer{
        padding-top: 5rem;
    }
    .footer h2{
        margin-bottom: 1rem;
    }
    .footer .fcontainer{
        width: 80%;
    }
    .footer li{
        line-height: 2.5;
    }
}