.destination-section{
    font-family: 'Times New Roman', Times, serif;
    display: flex;
    flex-direction: column;
}
.destination-section img{
    height: 250px;
}
.destination-ad{
    width: 60%;
    display: flex;
}
.destination-ad p{
    margin: 0 1.2em;
}
.dpara{
    padding: 0 1.2em;
    margin-top: 1.2em;
    text-align: center;
}
.destination-gallery{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(16em, 1fr));
}
.destination-gallery img{
    width: 100%;
}

@media screen and (max-width: 58em){
    .destination-ad{
        width: 100%;
        flex-direction: column-reverse;
    }
    .destination-ad p{
        text-align: center;
    }
    .destination-ad img{
        margin: 0 auto;
        width: 50%;
        height: auto;
    }
    .destination-gallery img{
        height: auto;
    }
}