.header{
    z-index: 9999;
    height: 70px;
    position: relative;
    background-color: var(--theme);
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    padding-right: 1.2em;
}

h1{
    font-family: 'Times New Roman', Times, serif;
    color: var(--themeColor);
}
.logo{
    height: 100%;
}
.logo img{
    height: 100%;
}
/* menu */

.header .menu{
    width: 100%;
    font-size: 16px;
    height: 0;    
    transition: height 0.2s ease-out;
    position: absolute;
    right: 0;
    top: 100%;
    background-color: var(--theme);
    overflow: hidden;
}
.header .menu li{
    font-family: 'Times New Roman', Times, serif;
    font-weight: 500;
    color: var(--themeColor);
    text-transform: uppercase;
    padding: 10px 2em;
}
.header .menu li:hover{
    cursor: pointer;
    color: #333;
    background-color: #f4f4f4;
}

/* menu btn */

.header .menu-btn{
    display: none;
}

.header .menu-btn:checked ~ .menu{
    height: calc(100vh - 70px);
}

.header .menu-btn:checked ~ .menu-icon .navicon{
    background: transparent;
}

.header .menu-btn:checked ~ .menu-icon .navicon::before{
    transform: rotate(-45deg);
}

.header .menu-btn:checked ~ .menu-icon .navicon::after{
    transform: rotate(45deg);
}

.header .menu-btn:checked ~ .menu-icon:not(.class) .navicon::before,
.header .menu-btn:checked ~ .menu-icon:not(.class) .navicon::after{
    top: 0;
}

/* menu icon */
.header .menu-icon {
    padding: 0 5px;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header .menu-icon .navicon{
    cursor: pointer;
    background: var(--themeColor);
    display: flex;
    height: 2px;
    position: relative;
    transition: background 0.2s ease-out;
    width: 22px;
}

.header .menu-icon .navicon::before,
.header .menu-icon .navicon::after{
    background: var(--themeColor);
    content: '';
    height: 100%;
    position: absolute;
    transform: all 0.2s ease-out;
    width: 100%;
}

.header .menu-icon .navicon::before{
    top: 7px;
}

.header .menu-icon .navicon::after{
    top: -7px;
}

/* 768px */

@media (min-width: 768px){

    .header ul{
        display: flex;
    }

    .header .menu{
        position: absolute;
        top: 0%;
        right: 1.2em;
        height: 100%;
        width: auto;
        font-size: 15px;
    }

    .header .menu ul{
        height: 100%;
    }

    .header .menu li{
        padding: 10px;
        height: 100%;
    }

    .header .menu-icon{
        display: none;
    }
}

