.home{
    border: 1px solid var(--themeColor);
    background-color: rgb(0, 0, 0);
    height: calc(100vh - 70px);
    position: relative;
    overflow: hidden;
}
.home .img{
    width: 100%;
    height: 100%;
    filter: brightness(0.5);
}
.home .video{
    position: absolute;
    width: 100%;
    height: 100%;
    filter: brightness(0.2);
}
.dot button{
    margin: 0 1.2em;
    cursor: pointer;
    border: transparent;
    background: transparent;
}
.dot button img{
    cursor: pointer;
    height: 25px;
}
.introTitle{
    line-height: normal;
    padding-left: 1.2em;
    position: absolute;
    z-index: 999;
    bottom: 3.2em;
}   
.introTitle h1{
    margin: 0;
    font-size: 4.5em;
}
.introTitle p{
    color: #fff;
    margin: 0;
    font-size: 1.2em;
}
.dot{
    position: absolute;
    bottom: 1.2em;
    width: 100%;
    z-index: 999;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.dots{
    cursor: pointer;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin: 0 5px;
    border: 1px solid #fff;
}
.activeCarousel{    
    background-color: var(--themeColor);
}
@media screen and (max-width: 58em){
    .introTitle{
        bottom: 4.2em;
    } 
    .introTitle h1{
        font-size: 2.2em;
    }
    .introTitle p{
        font-size: 16px;
    }
}