.tours{ 
    margin-top: 0px;
    padding: 1.2em;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.tours hr{
    width: 70%;
    margin: 1.2em auto;
}
.tnt-ad{
    margin: 1.2em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.tnt-ad img{
    height: 250px;
}
.tnt-ad p{
    text-align: center;
    margin: 0 1.2em;
}
.destination-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.destination-card{
    color: white;
    margin: 14px 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 310px;
}
.destination-card .places{
    height: 250px;
}
.destination-card span{
    font-size: 16px;
    background-color: var(--themeColor);
    text-align: center;
    border-radius: 0 0 8px 8px;
}
.destination-card a{
    font-style: italic;
    font-size: 12px;
    text-align: center;
}
.btn{
    display: flex;
    justify-content: center;
    margin: 1.2em 0 0 0;
}
.btn button{
    margin: 14px 0;
    padding: 10px;
    border: transparent;
    outline: none;
    background-color: var(--themeColor);
    border-radius: 5px;
    color: white;
}
@media screen and (min-width: 48em){
    .destination-container{        
        flex-direction: row;
        width: 100%;
    }
    .destination-card{
        margin: 0 14px;
        width: 245px;
    }
    .tnt-ad img{
        margin: 0 auto;
        width: 50%;
    }
    .tnt-ad p{
        text-align: left;
        width: 100%;
    }
}
@media screen and (min-width: 1125px){
    .tnt-ad{
        width: 70%;
        flex-direction: row;
    }
    .tnt-ad p{
        width: 40%;
    }
}