.contact-section{
    padding: 10px 1.2em;
    font-family: 'Times New Roman', Times, serif;
    line-height: normal;
    display: flex;
    flex-direction: column;
}

/* formContainer */
.formContainer{
    display: flex;
    justify-content: center;
    align-items: center;
}
.formBox{
    padding: 0 1em;
    /* box-shadow: 0 5px 10px -4px var(--themeColor); */
    overflow: hidden;
    width: 70%;
    height: 100%;
    min-height: 400px;
    display: flex;
    flex-direction: row;
    background-color: #ffffff;
}
.formBox .a{
    flex: 1;
    position: relative;
} 
.formBox .a img{
    width: 100%;
    position: absolute;
    bottom: 2em;
}
.formBox form{
    flex: 1;
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.formBox form div{
    width: 80%;
}
.formBox form label{
    color: var(--themeColor);
    margin: 0;
}
.formBox form input{
    all: unset;
    width: 100%;
    font-size: 16px;
    border: transparent;
    border-bottom: 2px solid #888;
    padding: 5px 0;
    display: block;
    margin: 0 0 12px 0;
}
.formBox form input[type="submit"]{
    all: unset;
    font-size: 16px;
    text-align: center;
    color: #fff;
    background-color: #218b8b;
    display: block;
    border: transparent;
    width: 20%;
    padding: 5px 1.2em;
    box-shadow: 0 3px 4px var(--themeColor);
}
.formBox .map{
    flex: 0.6;
    display: flex;
    flex-direction: column;
}
.formBox .mapHolder{
    flex: 1;
    background-image: url('../../img/undraw_traveling_t8y2.png');
    background-size: cover;
}
.formBox .mapDetails{
    flex: 0.5;
    font-size: 14px;
}


/* socialMedia */
.socialMedia{
    padding: 1.6em 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}
.socialMedia div{
    color: #fff;
    margin: 0 10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: var(--themeColor);
}

@media screen and (max-width: 48em){
    .formBox{
        height: 500px;
        width: 100%;
        flex-direction: column-reverse;
    }
    .formBox form div{
        width: 100%;
    }
    .formBox .a{
        display: none;
    }
    .mapDetails{
        display: none;
    }
}